import React from 'react'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'
import config from '../config'

import * as WEB3HOOKS from '../services/web3_hooks'
import Connect from '../components/connect'
import Loading from '../components/loading'
import ThankYou from './thank_you'
import RedeemFormComponent from './redeem_form'
import Image from '../components/image'

import ETHERIUM from '../services/etherium'
import EIP721 from '../services/etherium/contracts/eip721'
import IPFS from '../services/ipfs'

import * as Token from '../models/token'
import * as RedeemForm from '../models/redeem_form'

const doNothing = (e: React.MouseEvent<HTMLAnchorElement>) => {
  e.stopPropagation();
  return e
}

type Meta = {
  owner: string
  imageUrl: string
}
type MetaByTokenID = {[key: number]: Meta} // token id => Meta

type RedeemTokenState = {
  tokenId: number
  showRedeemForm: boolean
  showThankYou: boolean
}

function Reducer() {
  const context = useWeb3React<Web3Provider>()
  const { connector, chainId, account, library, deactivate, active, error } = context

  // handle logic to recognize the connector currently being activated
  const [activatingConnector, setActivatingConnector] = React.useState<any>()
  React.useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  // handle logic to eagerly connect to the injected ethereum provider, if it exists and has granted access already
  const triedEager = WEB3HOOKS.useEagerConnect()

  // handle logic to connect in reaction to certain events on the injected ethereum provider, if it exists
  WEB3HOOKS.useInactiveListener(!triedEager || !!activatingConnector)

  const [tokens, setTokens] = React.useState<Token.Token[] | undefined>(undefined)
  const [redeemForms, setRedeemForms] = React.useState<RedeemForm.RedeemForm[] | undefined>(undefined)
  
  const [redeemTokenState, setRedeemTokenState] = React.useState<RedeemTokenState | undefined>(undefined)

  // Load tokens from Firebase
  React.useEffect(() => {
    if (!!tokens) { return } // guard: already done
    if (!chainId) { return } // guard: already done

    (async () => {
      const tokens = await Token.indexByNetworkId(chainId!)
      setTokens(tokens)
    })()
  }, [tokens, chainId])

  // Get token owners
  const [metas, setMetas] = React.useState<MetaByTokenID | undefined>(undefined)
  React.useEffect(() => {
    if (!tokens) { return } // guard: tokens to be fetched
    if (!!metas) { return } // guard: already done
    if (!chainId) { return } // guard: chain

    const provider = ETHERIUM.getJsonRpcProvider(config.INFURA_JSON_RPC_API[chainId as 1 | 42], chainId as 1 | 42);
    
    (async () => {
      const clone = Object.assign({}, metas) as MetaByTokenID

      for(const token of tokens) {
        try {
          const contract = EIP721.getContract(token.contractAddress, provider);

          const owner = await (contract as any).ownerOf(token.tokenId)

          let imageUrl = ''
          try {
            const tokenURI = await (contract as any).tokenURI(token.tokenId)
            const tokenData = await (await fetch(tokenURI)).json()
            imageUrl = IPFS.readURL(tokenData.image)
          } catch(err) {
            console.error(`[ERC721] [ImageUrl] ${err}`)
          }

          clone[token.tokenId] = {
            owner,
            imageUrl
          }
        } catch (err) {
          console.error("[ERROR]", err)
        }
      }

      // console.log("tokenMeta:", clone)
      setMetas(clone)
    })()
  }, [metas, tokens])

  // Get account's redeem forms
  React.useEffect(() => {
    if (!account) { return } // account required
    if (!!redeemForms) { return } // guard: already done
    
    (async () => {
      const items = await RedeemForm.indexByOwner(account!)
      setRedeemForms(items)
    })()
  }, [account, redeemForms])

  if (!!account && !!redeemTokenState && !!redeemForms) {
    if (redeemTokenState!.showRedeemForm) {
      const redeemForm = redeemForms!.find(i => i.tokenId === redeemTokenState!.tokenId) || {
        tokenId: redeemTokenState!.tokenId,
        owner: account,
      }
      return (<RedeemFormComponent
        model={redeemForm}
        onSubmit={async (m) => {
          await RedeemForm.create(m)
          const items = await RedeemForm.index()
          setRedeemForms(items)
          setRedeemTokenState({ tokenId: m.tokenId, showRedeemForm: false, showThankYou: true })
        }}
        onClose={() => setRedeemTokenState(undefined)}
      />)
    }
    if (redeemTokenState!.showThankYou) {
      return <ThankYou/>
    }
  }

  const renderPage = () => {
    if (!account) {
      return (<h2 id="text33">Please connect to blockchain</h2>)
    }
    if (!tokens || !metas) {
      return (<><br/><Loading/></>)
    }

    const accountTokens = tokens!.filter(token => metas[token.tokenId] && metas[token.tokenId].owner === account)
    
    if (accountTokens.length === 0) {
      return (
        <div style={{ textAlign: 'left' }}>
          <h2 id="text33">Nothing you can claim at the moment</h2><br/>
          <h2 id="text33">Please visit our <a href="https://showcase.saltandsatoshi.com/">showcase</a></h2>
        </div>)
    }

    return accountTokens.map(token => {
      const redeemForm = (redeemForms || []).find(candidate => candidate.tokenId === token.tokenId)
      return (
        <Image
          key={token.tokenId}
          imageUrl={metas[token.tokenId].imageUrl}
        >
          { 
            !redeemForm && (
              <a href="#" className="button n01" onClick={event => {
                event.stopPropagation()
                setRedeemTokenState({
                  tokenId: token.tokenId,
                  showRedeemForm: true,
                  showThankYou: false
                })
                return false
              }}>
                <svg><use href="assets/icons.svg#download"></use></svg><span className="label">Claim</span>
              </a>)
          }
          { 
            !!redeemForm && (
              <a href="#" className="button n01" onClick={doNothing}>
                <span className="label">Already redeemed</span>
              </a>)
          }
        </Image>)
    })
  }


  return (
    <>
      <section id="redemption-section">
        <div id="container19" className="container default full screen" style={{minHeight: 'auto'}}>
          <div className="wrapper" style={{ backgroundColor: 'black' }}>
            <div className="inner" style={{padding: '3em 6em 1em 6em'}}>
              <h2 id="text33" style={{ fontFamily: "'Permanent Marker'" }}>Redeem collectible apparel</h2>
              { !account && (<p id="text34" className="style2">Verify ownership of Salt Season NFTs using</p>)}
              <Connect/>
            </div>
          </div>
        </div>
      </section>
      
      <section id="redemption2-section">
        <div id="container01" className="container columns full screen" style={{minHeight: 'auto'}}>
          <div className="wrapper">
            <div className="inner" style={{ display: "flex" }}>
              {renderPage()}
            </div>
          </div>
        </div>
      </section>
    </>)
}

export default Reducer